.cinema-modal-overlay {
  position: fixed;
  display: flex;

  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 20px;
  overflow-y: scroll;
  background-color: rgba(0, 0, 0, 0.25);

  z-index: 1000;
  &.is-hidden {
    display: none;
  }
}

body.onOpenModal {
  overflow: hidden;
}

.modal__cinema {
  position: relative;

  min-width: 280px;
  max-width: 320px;

  padding-top: 32px;
  padding-left: 20px;
  padding-right: 20px;
  margin: auto;

  background-color: $color-background;
}

.modal__cinema {
  // min-height: 1000px;

  @media screen and (min-width: 440px) {
    min-width: 280px;
    max-width: 500px;
  }

  @media screen and (min-width: 768px) {
    min-width: 600px;
    max-width: 700px;
  }

  @media screen and (min-width: 1024px) {
    min-width: 860px;
    max-width: 960px;
  }
}

.modal-icon-close {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 30px;
  height: 30px;
  transition: transform 200ms linear;
  color: #000;
  cursor: pointer;
  &:hover {
    transform: scale(1.2);
  }
}

.cinema-card {
  margin-top: 16px;
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: space-between;
    width: 560px;
  }
  @media screen and (min-width: 1024px) {
    width: 822px;
  }
}

.poster {
  margin-bottom: 30px;
  border-radius: 5px;
  width: 100%;
  height: 100%;
  @media screen and (min-width: 768px) {
    width: 50%;
    height: 50%;
  }
  @media screen and (min-width: 1024px) {
    width: 50%;
    height: 50%;
  }

  // @media screen and (min-width: 768px) {
  //   width: 264px;
  //   height: 373px;
  // }
  // @media screen and (min-width: 1024px) {
  //   width: 396px;
  //   height: 477px;
  // }
}

.cinema-title {
  font-family: Roboto;
  font-weight: 500;
  font-style: normal;
  font-size: 20px;
  line-height: 23px;

  margin-bottom: 20px;
}

.ratings-container {
  display: flex;
}

.ratings {
  margin-right: 40px;

  @media screen and (min-width: 1024px) {
    margin-right: 90px;
  }
}

.ratings-name {
  margin-bottom: 10px;
  &:nth-last-child(1) {
    margin-bottom: 20px;
  }
  font-family: Roboto;
  font-weight: 500;
  font-style: normal;
  font-size: 12px;
  line-height: 16px;
  color: #8c8c8c;
}

.rating-value {
  margin-bottom: 10px;
  &:nth-last-child(1) {
    margin-bottom: 20px;
  }
  font-family: Roboto;
  font-weight: 500;
  font-style: normal;
  font-size: 12px;
  line-height: 16px;
  color: #000;
}

.slash {
  color: #8c8c8c;
}

.current-value {
  border: #ff6b01;
  border-width: 1px;
  border-radius: 5px;
  border-style: solid;

  background-color: #ff6b01;
  color: #fff;

  padding-left: 10px;
  padding-right: 10px;
}

.about-title {
  font-family: Roboto;
  font-weight: 500;
  font-style: normal;
  font-size: 12px;
  line-height: 16px;

  margin-bottom: 10px;
}

.about-text {
  font-family: Roboto;
  font-weight: 500;
  font-style: normal;
  font-size: 12px;
  line-height: 20px;

  margin-bottom: 21px;
}

.modal-buttons-container {
  display: flex;
  justify-content: space-between;

  @media screen and (min-width: 1024px) {
    justify-content: flex-start;
  }
}

.modal-button {
  width: 112px;
  height: 44px;
  margin-bottom: 41px;
  cursor: pointer;

  font-family: Roboto;
  font-weight: 500;
  font-style: normal;
  font-size: 11px;
  line-height: 16px;
  text-transform: uppercase;

  background-color: #fff;
  border-width: 1px;
  border-radius: 5px;
  border-style: solid;
  transition: 250ms background cubic-bezier(0.4, 0, 0.2, 1),
    250ms color cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  :focus {
    background: #ff6b01;
    box-shadow: rgba(255, 107, 1, 0.6);
    border: none;
    color: #fff;
  }
  @media screen and (min-width: 768px) {
    width: 125px;
  }

  @media screen and (min-width: 1024px) {
    width: 136px;
  }
}

.button-watched {
  cursor: pointer;
  @media screen and (min-width: 1024px) {
    margin-right: 15px;
  }
}

.description-container {
  @media screen and (min-width: 768px) {
    width: 264px;
  }
  @media screen and (min-width: 1024px) {
    width: 396px;
  }
}
